import * as types from './mutation-types.js'

import {
  setStore
} from '../config/utils'

// import {localapi, proapi} from 'src/config/env'

export default {
  [types.OPEN_IS_LOGIN] (state, isLogin) {
    if (isLogin.status === '1') {
      state.isLogin = true
    } else {
      state.isLogin = false
      state.userInfo = isLogin
    }
    // 存入localStorage
    setStore('isLogin', state.isLogin)
    // console.log(getStore('bannerList'))
  }
}



