/**
 * 下载队列
 */
import {
  downLoadItem,
  downLoadStatus,
  fileError
} from './downloadItem'
import {
  setStore,
  getStore
} from './utils'
// 根路径
var rootPath = ''
var curTargetDir = 'video'
// let _app = undefined
// 对象
var $Cordova = {
  file: {},
  download: {}
}
var DownLoadItem = downLoadItem
// 设备准备好事件
// document.addEventListener('deviceready', onDeviceReady, false)

// 设备准备好方法
const onDeviceReady = function () {
  // console.log('ready')
  initFile()
  initDownload()
  if (window.plugins) {
    window.plugins.file = $Cordova.file
    window.plugins.downloads = $Cordova.download
  }
}
// 初始化
const init = function () {
  document.addEventListener('deviceready', onDeviceReady, false)
}

// 初始化文件系统
const initFile = function () {
  $Cordova.file.checkDir = function (path, dir, result) {
    if ((/^\//.test(dir))) {
      console.error('directory cannot start with /')
    }
    try {
      let directory = path + dir
      // console.log(directory)
      window.resolveLocalFileSystemURL(directory, function (fileSystem) {
        if (fileSystem.isDirectory === true) {
          result(fileSystem)
        } else {
          console.error('input is not a directory')
          result(undefined)
        }
      }, function (error) {
        console.error(fileError[error.code])
        result(undefined)
      })
    } catch (err) {
      console.error(fileError[err.code])
      result(undefined)
    }
  }
  $Cordova.file.checkFile = function (path, file, result) {
    if ((/^\//.test(file))) {
      console.error('directory cannot start with /')
    }

    try {
      let directory = path + file
      // console.log(directory)
      // console.log('checkFile!!!!============')
      // directory = 'cdvfile://localhost/video/' + file
      window.resolveLocalFileSystemURL(directory, function (fileSystem) {
        if (fileSystem.isFile === true) {
          result(fileSystem)
        } else {
          console.error('input is not a file')
          result(undefined)
        }
      }, function (error) {
        console.error(fileError[error.code])
        result(undefined)
      })
    } catch (err) {
      console.error(fileError[err.code])
      result(undefined)
    }
  }
  $Cordova.file.createDir = function (path, dirName, replaceBool, result) {
    if ((/^\//.test(dirName))) {
      console.error('directory cannot start with /')
    }

    replaceBool = !replaceBool

    let options = {
      create: true,
      exclusive: replaceBool
    }

    try {
      window.resolveLocalFileSystemURL(path, function (fileSystem) {
        fileSystem.getDirectory(dirName, options, function (_result) {
          result(_result)
        }, function (error) {
          console.error(fileError[error.code])
          result(undefined)
        })
      }, function (err) {
        console.error(fileError[err.code])
        result(undefined)
      })
    } catch (e) {
      console.error(fileError[e.code])
      result(undefined)
    }
  }
  $Cordova.file.createFile = function (path, fileName, replaceBool) {}
  $Cordova.file.removeDir = function (path, dirName) {}
  $Cordova.file.removeFile = function (path, fileName, result) {
    if ((/^\//.test(fileName))) {
      console.log('file-name cannot start with /')
    }

    try {
      window.resolveLocalFileSystemURL(path, function (fileSystem) {
        fileSystem.getFile(fileName, {
          create: false
        }, function (fileEntry) {
          fileEntry.remove(function () {
            result({
              success: true,
              fileRemoved: fileEntry
            })
          }, function (error) {
            console.error(fileError[error.code])
            result({
              success: false
            })
          })
        }, function (err) {
          console.error(fileError[err.code])
          result({
            success: false
          })
        })
      }, function (er) {
        console.error(fileError[er.code])
        result({
          success: false
        })
      })
    } catch (e) {
      console.error(fileError[e.code])
      result({
        success: false
      })
    }
  }
  $Cordova.file.removeRecursively = function (path, dirName) {}
  $Cordova.file.writeFile = function (path, fileName, text, replaceBool) {}
  $Cordova.file.writeExistingFile = function (path, fileName, text) {}
  $Cordova.file.readAsText = function (path, file) {}
  $Cordova.file.readAsDataURL = function (path, file) {}
  $Cordova.file.readAsBinaryString = function (path, file) {}
  $Cordova.file.readAsArrayBuffer = function (path, file) {}
  $Cordova.file.moveFile = function (path, fileName, newPath, newFileName, result) {
    newFileName = newFileName || fileName

    if ((/^\//.test(fileName)) || (/^\//.test(newFileName))) {
      console.error('file-name cannot start with /')
    }

    try {
      window.resolveLocalFileSystemURL(path, function (fileSystem) {
        fileSystem.getFile(fileName, {
          create: false
        }, function (fileEntry) {
          window.resolveLocalFileSystemURL(newPath, function (newFileEntry) {
            fileEntry.moveTo(newFileEntry, newFileName, function (_result) {
              result(_result)
            }, function (error1) {
              result(undefined)
            })
          }, function (erro) {
            result(undefined)
          })
        }, function (err1) {
          result(undefined)
        })
      }, function (er) {
        result(undefined)
      })
    } catch (e) {
      result(undefined)
    }
  }
  $Cordova.file.moveDir = function (path, dirName, newPath, newDirName) {}
  $Cordova.file.copyDir = function (path, dirName, newPath, newDirName) {}
  $Cordova.file.copyFile = function (path, fileName, newPath, newFileName) {}

  $Cordova.file.bytesToSize = function (bytes) {
    if (bytes === undefined || bytes === 0) return '0 B'
    let k = 1024
    let sizes = ['B', 'KB', 'MB', 'GB']
    let i = Math.floor(Math.log(bytes) / Math.log(k))
    return ((bytes / Math.pow(k, i))).toPrecision(3) + ' ' + sizes[i]
  }
}

// 初始化下载
const initDownload = function () {
  $Cordova.download = (function () {
    // let _self = this
    // let _utils = undefined

    let options = {
      'debug': 'false'// true、false
    }
    // 下载队列
    let downloadQueue = []
    // 当前资源
    let downloadFile

    let curDownloadUrl = ''
    let targetPath = ''// 目标目录
    let _filename = ''// 文件名称
    let tmpFileName = ''// 临时文件名称

    // 下载状态
    const isDownloading = function () {
      // for (let i = 0; i < downloadQueue.length; i++) {
      //   if (downloadQueue[i].status === downLoadStatus.DOWNLOADING) {
      //     return true
      //   }
      // }
      // return false
      // console.log('!!!!!!!!!!!!!')
      // console.log(JSON.stringify(downloadQueue.some(obj => obj.status === downLoadStatus.DOWNLOADING)))
      return downloadQueue.some(obj => obj.status === downLoadStatus.DOWNLOADING)
    }
    // 删除资源
    const removeDownloadQueueByIndex = function (index) {
      let object = downloadQueue[index]
      downloadQueue.splice(index, 1)
      return object
    }
    // 删除资源
    const removeDownloadQueue = function (resourceId) {
      let i = 0
      for (;i < downloadQueue.length; i++) {
        if (downloadQueue[i].resourceId === resourceId) {
          break
        }
      }
      if (i >= downloadQueue.length) {
        return undefined
      } else {
        return removeDownloadQueueByIndex(i)
      }
    }
    // 获取要下载的资源信息
    const getDownloadInfo = function (resId) {
      // console.log('resID:' + resId)
      if (resId === undefined) {
        // for (let i = 0; i < downloadQueue.length; i++) {
        //   if (downloadQueue[i].status === downLoadStatus.NO_START || downloadQueue[i].status === downLoadStatus.WAITING) {
        //     return downloadQueue[i]
        //   }
        // }
        // console.log(downloadQueue)
        return downloadQueue.find(obj => obj.status === downLoadStatus.NO_START || obj.status === downLoadStatus.WAITING)
        // return undefined
      } else {
        // for (let i = 0; i < downloadQueue.length; i++) {
        //   if (downloadQueue[i].resourceId === resourceId) {
        //     return downloadQueue[i]
        //   }
        // }
        // console.log(downloadQueue)
        return downloadQueue.find(obj => obj.resourceId === resId)
        // return undefined
      }
    }
    // 添加到下载队列
    const _push = function (resId, resDownloadUrl, resOrderby, courseId, resFile, resName) {
      let _result = {
        success: true,
        data: {
          resId: undefined
        }
      }
      try {
        // let tmpResource = _.find(downloadQueue, {
        //   'resourceId': resId
        // })
        let tmpResource = getDownloadInfo(resId)
        // let _downloadModel = new downloadItem.DownloadItem(resId, resDownloadUrl, resOrderby, courseId, resFile)

        if (tmpResource === undefined) {
          let _downloadModel = Object.assign({}, DownLoadItem)

          // _downloadModel = new downloadItem.DownloadItem()
          _downloadModel.resourceId = resId // 资源ID
          _downloadModel.resourceDownloadUrl = resDownloadUrl // 资源下载地址url
          _downloadModel.resourceIndex = resOrderby // 资源索引用于课程排序
          _downloadModel.belongsCourseId = courseId // 所属课程ID
          _downloadModel.resourceFile = resFile // 资源文件名
          _downloadModel.resourceName = resName // 资源显示名

          downloadQueue.push(_downloadModel)
        } else {
          _result.success = false
        }
        if (resId !== undefined) {
          _result.data.resId = resId
        }

        setTimeout(function () {
          // console.log('startDownload5:' + _result.data.resId)
          _start(_result.data.resId)
        }, 300)
      } catch (e) {
        _result.success = false
      }

      window.cordova.fireWindowEvent('download.pushed', _result)

      window.cordova.fireWindowEvent('download.list.saved', {})
    }
    // 初始化保存路径
    const initTargetPath = function (_callback) {
      // console.log('initTargetPath', curTargetDir, $Cordova.file.checkDir)
      $Cordova.file.checkDir(rootPath, curTargetDir, function (checkDirResult) {
        // console.log('checkDir', checkDirResult)
        // console.log('checkDir')
        // console.log(JSON.stringify(checkDirResult))
        if (checkDirResult === undefined) {
          $Cordova.file.createDir(rootPath, curTargetDir, false, function (createDirResult) {
            if (createDirResult.isDirectory) {
              targetPath = createDirResult.toURL().replace('file://', '') + tmpFileName
              console.log('create targetPath:' + targetPath)
              _callback()
            }
          })
        } else {
          if (checkDirResult.isDirectory) {
            targetPath = checkDirResult.toURL().replace('file://', '') + tmpFileName
            // console.log('targetPath:' + targetPath)
            _callback()
          }
        }
      })
    }
    // 成功以后设置视频播放路径
    const setPlayUrl = function (fileName) {
      $Cordova.file.checkFile(rootPath + curTargetDir + '/', fileName, function (checkFileResult) {
        if (checkFileResult !== undefined) {
          // downloadFile.resourceTargetPath = checkFileResult.toURL();
          downloadFile.resourceTargetPath = curTargetDir + '/' + fileName
        } else {
          downloadFile.resourceTargetPath = ''
        }
        window.cordova.fireWindowEvent('download.list.saved', {})
      })
    }
    // 初始化插队列表
    const initJumpQueueList = function () {
      if (downloadQueue.length > 0) {
        for (let i = 0; i < downloadQueue.length; i++) {
          if (downloadQueue[i].status === downLoadStatus.NO_START || downloadQueue[i].status === downLoadStatus.DOWNLOADING) {
            // 修改资源状态
            downloadQueue[i].status = downLoadStatus.WAITING
            // 暂停下载资源
            // console.log('startDownload6', downloadQueue)
            _pause(downloadQueue[i].resourceId)
          }
        }
      }
      // let obj = downloadQueue.find(obj => obj.status === downLoadStatus.NO_START || obj.status === downLoadStatus.DOWNLOADING)
      // if (obj !== undefined) {
      //   obj.status = downLoadStatus.WAITING
      //   _pause(obj.resourceId)
      // }
    }

    // 开始下载
    const startDownload = function (_downloadUrl, _targetPath, _tmpFileName, _fileName, _options) {
      $Cordova.file.checkFile(rootPath + curTargetDir + '/', _fileName, function (checkFileResult) {
        if (checkFileResult === undefined) {
          if (!isDownloading()) {
            // 开始下载...
            downloadFile.status = downLoadStatus.DOWNLOADING
            let startDateTime = new Date().getTime()
            let endDateTime = 0
            window.plugins.download.start(_downloadUrl, _targetPath, _options, function (data) {
              // 下载进度...
              window.plugins.download.progress(_downloadUrl, _options, function (data) {
                // data = data.data
                // console.log(JSON.stringify(data))
                if (data.data.error !== undefined) {
                  $Cordova.file.removeFile(rootPath + curTargetDir, _tmpFileName, function (removeFileResult) {
                  })
                  return
                }

                if (data.data !== undefined && (data.data.isCompletd === false || data.data.isCompletd === 'false')) {
                  // function _1s (data) {
                  //   console.log(downloadFile, data, 'progress!!!!!!!!!')
                  //   downloadFile.speed = data.speed
                  //   downloadFile.loaded = data.loaded
                  //   downloadFile.fileSize = data.total
                  //
                  //   window.cordova.fireWindowEvent('download.progress',
                  //     {
                  //       success: true, // true, false
                  //       data: {
                  //         resId: downloadFile.resourceId,
                  //         speed: data.speed,
                  //         loaded: data.loaded,
                  //         total: data.total,
                  //         // 磁盘信息
                  //         disk: {
                  //           // 空闲
                  //           free: data.freeDisk,
                  //           // 磁盘总大小
                  //           total: data.totalDisk
                  //
                  //         }
                  //       }
                  //     })
                  //   window.cordova.fireWindowEvent('download.list.saved', {})
                  // }
                  endDateTime = new Date().getTime()
                  // console.log('startDownload10', data, endDateTime - startDateTime, downloadFile)
                  if (endDateTime - startDateTime >= 200) {
                    startDateTime = endDateTime
                    // console.log('1s 刷新')
                    // _1s(data)
                    downloadFile.speed = data.data.speed
                    downloadFile.loaded = data.data.loaded
                    downloadFile.fileSize = data.data.total

                    window.cordova.fireWindowEvent('download.progress',
                      {
                        success: true, // true, false
                        data: {
                          resId: downloadFile.resourceId,
                          speed: data.data.speed,
                          loaded: data.data.loaded,
                          total: data.data.total,
                          // 磁盘信息
                          disk: {
                            // 空闲
                            free: data.data.freeDisk,
                            // 磁盘总大小
                            total: data.data.totalDisk

                          }
                        }
                      })
                    window.cordova.fireWindowEvent('download.list.saved', {})
                  }
                } else {
                  // console.log('startDownload11', rootPath + curTargetDir, _tmpFileName, rootPath + curTargetDir, _fileName, downloadFile)
                  $Cordova.file.moveFile(rootPath + curTargetDir, _tmpFileName, rootPath + curTargetDir, _fileName, function (result) {
                    window.cordova.fireWindowEvent('download.complete',
                      {
                        success: true,
                        data: {
                          resId: downloadFile.resourceId
                        }
                      })
                    downloadFile.status = downLoadStatus.COMPLETE
                    // window.cordova.fireWindowEvent('download.list.saved', {})
                    setPlayUrl(_fileName)
                    setTimeout(function () {
                      // console.log('startDownload1', $Cordova.download.list())
                      _start()
                    }, 300)
                  })
                }
              })
            })
          }
        } else {
          // console.log('startDownload17')
          window.cordova.fireWindowEvent('download.complete',
            {
              success: true,
              data: {
                resId: downloadFile.resourceId
              }
            })
          downloadFile.status = downLoadStatus.COMPLETE
          // window.cordova.fireWindowEvent('download.list.saved', {})

          setPlayUrl(_fileName)

          setTimeout(function () {
            // console.log('startDownload2')
            _start()
          }, 300)
        }
      })
    }
    // 开始下载,如果不传入资源id,全部开始下载
    const _start = function (resourceId) {
      // alert("_start"+resourceId)
      // console.log('_start:' + resourceId)
      if (resourceId === undefined && isDownloading()) {
        return
      }

      if (resourceId !== undefined) {
        initJumpQueueList()
      }

      let _result = {
        success: true,
        data: {
          resId: undefined
        }
      }
      try {
        downloadFile = getDownloadInfo(resourceId)
        // console.log('_start1:', downloadFile)
        if (downloadFile === undefined) {
          _result.success = false
          window.cordova.fireWindowEvent('download.started', _result)
          return
        }
        curDownloadUrl = downloadFile.resourceDownloadUrl
        // _filename = curDownloadUrl.substring(curDownloadUrl.lastIndexOf("/")+1);
        _filename = downloadFile.resourceFile
        tmpFileName = _filename + '.tmp'

        _init(function () {
          // 初始化 targetPath
          // console.log(rootPath, 'init')
          initTargetPath(function () {
            startDownload(curDownloadUrl, targetPath, tmpFileName, _filename, options)
          })
        })
        _result.data.resId = resourceId
      } catch (e) {
        _result.success = false
      }

      window.cordova.fireWindowEvent('download.started', _result)
    }
    // 暂停下载,如果不传入资源id,全部开始暂停
    const _pause = function (resourceId) {
      if (resourceId === undefined) {
        return
      }
      let _result = {
        success: true,
        data: {
          resId: undefined
        }
      }
      try {
        let _resource = __getResource(resourceId)
        if (_resource !== undefined && _resource.status === downLoadStatus.DOWNLOADING) {
          // if(_resource !==undefined &&_resource.status ===downLoadStatus.DOWNLOADING){
          _resource.status = downLoadStatus.PAUSE
        }
        // console.log("_pause"+_resource.resourceDownloadUrl)
        // 暂停下载...
        window.plugins.download.pause(_resource.resourceDownloadUrl, options, function (data) {
          setTimeout(function () {
            // console.log('startDownload3')
            // if (_resource.status !== downLoadStatus.PAUSE) {
            //   _start(resourceId)
            // } else {
            //   _start()
            // }
            _start()
            _result.data.resId = resourceId

            window.cordova.fireWindowEvent('download.list.saved', {})
          }, 300)
        })
      } catch (e) {
        _result.success = false
      } finally {
        window.cordova.fireWindowEvent('download.paused', _result)
      }
    }
    // 终止下载,如果不传入资源id
    const _abort = function (resourceId) {
      if (resourceId === undefined) {
        return
      }

      let _result = {
        success: true,
        data: {
          resId: undefined
        }
      }
      try {
                    // 删除数据
        let removeRes = removeDownloadQueue(resourceId)
                    // 终止下载...
        window.plugins.download.abort(removeRes.resourceDownloadUrl, options, function (data) {
          let _file = removeRes.resourceTargetPath.substring(removeRes.resourceTargetPath.lastIndexOf('/') + 1)

                        // console.log("remove : "+_file);

          _init(function () {
            $Cordova.file.removeFile(rootPath + curTargetDir, _file
                                , function (removeFileResult) {
                                    // 删除成功
                                  if (removeFileResult.success) {

                                  }
                                })
          })

          if (downloadFile !== undefined && _file.resourceId === downloadFile.resourceId) {
            setTimeout(function () {
              // console.log('startDownload4')
              _start()
            }, 300)
          }
        })

        _result.data.resId = resourceId
      } catch (e) {
        _result.success = false
      }

      window.cordova.fireWindowEvent('download.aborted', _result)

      window.cordova.fireWindowEvent('download.list.saved', {})
    }
    // 获取下载列表,通过课程ID
    const _list = function () {
      _init(function () {
        let _result = {
          success: true,
          data: {
            resList: []
          }
        }
        try {
          if (downloadQueue.length > 0) {
            for (let i = 0; i < downloadQueue.length; i++) {
              // let tmpDownloadFile = downloadQueue[i]
              _result.data.resList.push({
                courseId: downloadQueue[i].belongsCourseId,
                resId: downloadQueue[i].resourceId,
                resName: downloadQueue[i].resourceName,
                resTargetPath: rootPath + downloadQueue[i].resourceTargetPath,
                resDownloadPath: downloadQueue[i].resourceDownloadUrl,
                resStatus: downloadQueue[i].status,
                resLoaded: $Cordova.file.bytesToSize(downloadQueue[i].loaded),
                resFileSize: $Cordova.file.bytesToSize(downloadQueue[i].fileSize),
                resLoadedSource: downloadQueue[i].loaded,
                resFileSizeSource: downloadQueue[i].fileSize
              })
            }
          }
        } catch (e) {
          _result.success = false
        }
        window.cordova.fireWindowEvent('download.list', _result)
                    // console.log(_result);
      })
    }
    // 开启仅wifi下载
    const _enableWifiDownload = function () {
      setStore('download.wifi.download', true)
    }
    // 关闭仅wifi下载
    const _disableWifiDownload = function () {
      setStore('download.wifi.download', false)
    }
    // 是否启动wifi下载
    const _isEnableWifiDownload = function () {
      let _result = JSON.parse(getStore('download.wifi.download'))
      if (_result === undefined) {
        _enableWifiDownload()
      }
      return JSON.parse(getStore('download.wifi.download'))
    }

    // 获取资源
    const __getResource = function (resourceId) {
      // for (let i = 0; i < downloadQueue.length; i++) {
      //   if (downloadQueue[i].resourceId === resourceId) {
      //     return downloadQueue[i]
      //   }
      // }
      // return undefined
      return getDownloadInfo(resourceId)
    }
    const _getResource = function (resourceId) {
      let _resource = __getResource(resourceId)

      if (rootPath === undefined || rootPath === '') {
        // 初始化程序根路径
        _init(function () {})
      }

      if (_resource !== undefined && _resource.resourceId === resourceId) {
        return {
          courseId: _resource.belongsCourseId,
          resId: _resource.resourceId,
          resTargetPath: rootPath + _resource.resourceTargetPath,
          resDownloadPath: _resource.resourceDownloadUrl,
          resStatus: _resource.status,
          resLoaded: $Cordova.file.bytesToSize(_resource.loaded),
          resFileSize: $Cordova.file.bytesToSize(_resource.fileSize)
        }
      }
      return undefined
    }

    // 初始化
    const _init = function (_callback) {
      let platform = window.device.platform
      if (platform === 'Android') {
        rootPath = window.cordova.file.externalDataDirectory
      } else if (platform === 'iOS') {
        rootPath = window.cordova.file.documentsDirectory + 'NoCloud/'
      }
      _callback()
    }

    window.addEventListener('download.list.saved', function (e) {
      // console.log('saved')
      // console.log(downloadQueue)
      setStore('download_list', downloadQueue)
      _list()
    })

    const _initQueue = function () {
      // _utils = e._utils
      // _app = e._app

      // let curUser = getStore('curUser')
      //
      // if (curUser !== undefined && curUser !== '') {
        // let username = curUser.username
        // _utils.userKey = username
      // }

      // let dlList = getStore('download_list')

      let strlist = getStore('download_list')
      let dlList = JSON.parse(strlist)

      if (dlList === null) {
        return
      }

      for (let i = 0; i < dlList.length; i++) {
        // let tmpDownloadInfo = dlList[i]
          // let _downloadModel = new downloadItem.DownloadItem(...tmpDownloadInfo)
        let _downloadModel = Object.assign({}, dlList[i])

        // _downloadModel.resourceId = tmpDownloadInfo.resourceId // 资源ID
        // _downloadModel.resourceDownloadUrl = tmpDownloadInfo.resourceDownloadUrl // 资源下载地址url
        // _downloadModel.resourceIndex = tmpDownloadInfo.resourceIndex // 资源索引用于课程排序
        // _downloadModel.resourceTargetPath = tmpDownloadInfo.resourceTargetPath// 本地存储目录
        // _downloadModel.resourceName = tmpDownloadInfo.resourceName// 显示名称
        // _downloadModel.belongsCourseId = tmpDownloadInfo.belongsCourseId// 所属课程ID
        _downloadModel.speed = '0'
        // _downloadModel.progress = tmpDownloadInfo.progress
        // _downloadModel.loaded = tmpDownloadInfo.loaded
        // _downloadModel.fileSize = tmpDownloadInfo.fileSize

        if (dlList[i].loaded !== 0 && dlList[i].loaded === dlList[i].fileSize) {
          _downloadModel.status = downLoadStatus.COMPLETE// 未开始、正在下载、暂停、已完成
        } else {
          _downloadModel.status = dlList[i].status// 未开始、正在下载、暂停、已完成
        }

        if (_downloadModel.status === downLoadStatus.DOWNLOADING) {
          _downloadModel.status = downLoadStatus.PAUSE// 未开始、正在下载、暂停、已完成
        } else {
          _downloadModel.status = dlList[i].status
        }

        downloadQueue.push(_downloadModel)
      }

      // }
    }
    _initQueue()
    return {

      push: function (resId, resDownloadUrl, resOrderby, courseId, resFile) {
        return _push(resId, resDownloadUrl, resOrderby, courseId, resFile)
      },
      start: function (resourceId) {
        _start(resourceId)
      },
      pause: function (resourceId) {
        _pause(resourceId)
      },
      abort: function (resourceId) {
        _abort(resourceId)
      },
      list: function () {
        _list()
      },
      enableWifiDownload: function () {
        _enableWifiDownload()
      },
      disableWifiDownload: function () {
        _disableWifiDownload()
      },
      isEnableWifiDownload: function () {
        return _isEnableWifiDownload()
      },
      getResource: function (resourceId) {
        return _getResource(resourceId)
      }
    }
  })()
}

export default {init}



