const downLoadStatus = {
  NO_START: 'no_start',
  WAITING: 'waiting',
  DOWNLOADING: 'downloading',
  PAUSE: 'pause',
  COMPLETE: 'complete'
}
const fileError = {
  1: 'NOT_FOUND_ERR',
  2: 'SECURITY_ERR',
  3: 'ABORT_ERR',
  4: 'NOT_READABLE_ERR',
  5: 'ENCODING_ERR',
  6: 'NO_MODIFICATION_ALLOWED_ERR',
  7: 'INVALID_STATE_ERR',
  8: 'SYNTAX_ERR',
  9: 'INVALID_MODIFICATION_ERR',
  10: 'QUOTA_EXCEEDED_ERR',
  11: 'TYPE_MISMATCH_ERR',
  12: 'PATH_EXISTS_ERR'
}
const downLoadItem = {
  resourceId: '', // 资源ID
  resourceFile: '', // 资源文件名
  resourceName: '', // 资源显示名
  resourceDownloadUrl: '', // 资源下载地址url
  resourceIndex: 0, // 资源索引用于课程排序
  resourceTargetPath: '', // 本地存储目录
  belongsCourseId: '', // 所属课程ID
  status: 'no_start', // 未开始、正在下载、暂停、已完成
  speed: '0',
  progress: '0%',
  loaded: '0',
  fileSize: '0'
}

export {
  downLoadItem,
  downLoadStatus,
  fileError
}

